.home-5
	padding-top: 0 !important

.category-list
	.category-zone
		display: flex
		align-items: center
		flex-wrap: wrap
		margin-left: -15px
		margin-bottom: -15px
		li
			padding-left: 15px
			padding-bottom: 15px
			a
				height: 34px
				border-radius: 17px
				padding: 0 15px
				display: flex
				align-items: center
				justify-content: center
				font-weight: 700
				&.active
					background: color(main)
					color: #fff
	+ .category-list
		margin-top: r(40px)
	@media screen and ( max-width: 768.98px )
		.section-heading
			flex-wrap: wrap
			.section-title
				margin-bottom: 10px
