.news-list
	@media screen and ( max-width: 1024.98px )
		display: flex
		flex-wrap: wrap
		margin: 0 -10px -20px
		> *
			width: 50%
			padding: 0 10px 20px
	@media screen and ( max-width: 576px )
		> *
			width: 100%
		
	@media screen and ( min-width: 1024.98px )
		margin-left: -30px
		&:after
			display: block
			content: ''
			clear: both
		.news-col
			&:nth-child(1)
				float: left
				width: 50%
				padding-right: 10px
				padding-left: 30px
			&:nth-child(2)
				width: 25%
				float: left
				padding-left: 20px
				margin-bottom: 15px
			&:nth-child(3)
				width: 25%
				float: left
				padding-left: 20px
				margin-bottom: 15px
			&:nth-child(4)
				width: 25%
				float: left
				padding-left: 20px
				margin-bottom: 0
			&:nth-child(5)
				width: 25%
				float: left
				padding-left: 20px
				margin-bottom: 0
			&:nth-child(n + 6)
				float: left
				width: 33.33%
				margin-top: 30px
				padding-left: 30px

.news-item
	border-radius: 10px
	overflow: hidden
	display: block
	position: relative
	.news-caption
		background-color: rgba(#000, 0.5)
		transition: 0.3s all
		color: #fff
		height: 60px
		position: absolute
		bottom: 0
		left: 0
		width: 100%
		display: flex
		align-items: center
		.title
			line-height: 1.375
			font-weight: 700
	&:hover
		.news-caption
			background-color: rgba(color(main), 0.85)
	&.news-item-big
		.news-img
			+img-scale(434 / 590 * 100%)
		.news-caption
			padding: 0 15px
			.title
				+line(1)
				font-size: r(18px)
	&.news-item-small
		.news-img
			+img-scale(193 / 272 * 100%)
		.news-caption
			padding: 0 10px
			.title
				font-size: r(14px)
				+line(2)