.article-content
	line-height: 1.5
	> *
		+ *
			margin-top: r(20px)
	ul
		list-style: disc
		padding-left: 1.25rem
	table
		td
			border: 1px solid #dee2e6
			padding: .75rem
			vertical-align: top
	h2
		font-size: r(20px)
		font-weight: 700
	h3
		font-size: r(18px)
		font-weight: 600
	* + h2
		margin-top: r(28px)

.expand-content
	overflow: hidden
	&.show
		max-height: none !important

.expand-toggle
	display: flex
	align-items: center
	justify-content: space-between
	padding: 0 r(20px)
	margin-top: r(20px)
	background: color(main)
	color: #fff
	width: r(150px)
	height: r(40px)
	border-radius: r(20px)
	margin-left: auto
	margin-right: auto
	text-transform: uppercase
	span
		margin-left: 8px
		font-size: r(18px)