header
	position: relative
	top: 0
	left: 0
	width: 100%
	background: #fff

	.top-header
		background: #fafafa
		border-bottom: 1px solid #f2f2f2

	.middle-header
		.row
			margin-bottom: 0
			> *
				padding-bottom: 0

	.logo,.vn-pay-banner
		transition: 0.3s all
		max-width: 180px

	.header-util-list
		.header-util-item
			min-height: 20px
			display: flex
			align-items: center
			+ .header-util-item
				margin-left: 15px
				padding-left: 15px
				position: relative
				&:before
					position: absolute
					content: ''
					top: 0
					left: 0
					bottom: 0
					width: 1px
					background: #cccccc

	.hotline
		text-align: right
		display: flex
		align-items: center
		height: 40px
		border-left: 1px solid #cccccc
		justify-content: flex-end
		padding-left: 15px
		a
			em
				color: map-get($colors, main)
				font-size: 18px
				position: relative
				top: 2px
			strong
				font-weight: 900
				font-size: 20px
				color: map-get($colors, main)
				margin: 0 8px
			span
				font-size: 14px

	.language
		.material-icons
			color: #cccccb
			margin-left: 10px
		.dropdown-content
			padding-top: 10px
			min-width: 51px
			right: auto
			left: 50%
			transform: translate(-50%)
			ul
				padding: 15px
				li
					+ li
						margin-top: 10px
	
	.order-check
		a
			font-size: 14px
			display: flex
			align-items: center
			em
				font-size: 18px
				color: color(main)
				margin-right: 5px

	.account-wrapper
		padding-left: 0 !important
		border-left: 0 

	.account
		.firstnav
			> span
				font-weight: 700
				white-space: nowrap
		> a
			height: 39px
			white-space: nowrap
			display: flex
			align-items: center
			justify-content: center
			background: color(main)
			padding: 0 15px
			span
				margin: 0 8px
				font-size: 14px
				overflow: hidden
				text-overflow: ellipsis
				color: #fff
				line-height: 18px
			em
				font-size: 20px
				color: #713809
			.arrow
				font-size: 18px
				color: #fff
		.dropdown-content
			right: 0
			ul
				li
					+ li
						margin-top: 10px
				a
					white-space: nowrap
	
	.middle-header
		padding: 20px 0
		transition: 0.3s all

	.search-wrapper
		padding-left: r(45px)
		.searchbox
			display: flex
			align-items: center
			width: 100%
			position: relative
			input
				height: 40px
				flex: 1
				border: 2px solid map-get($colors, main)
				padding: 0 15px
				font-size: 14px
				border-radius: 4px 0 0 4px
				&::placeholder
					color: #999
			button
				height: 40px
				width: 45px
				background: map-get($colors, main)
				border: 0
				color: #fff
				font-size: 24px
				display: flex
				align-items: center
				justify-content: center
				cursor: pointer
				border-radius: 0 4px 4px 0
		.suggestsearch
			position: absolute
			top: 100%
			padding: 20px
			background: #f9f9f9
			border-radius: 10px
			border: 1px solid #eeeeee
			box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.15)
			margin-top: 10px
			z-index: 10
			display: none
			width: 100%
			max-height: 70vh
			overflow: auto
			.row
				flex-wrap: wrap !important
			&::-webkit-scrollbar
				width: 5px
				background: #ccc
			&::-webkit-scrollbar-thumb
				background: map-get($colors, blue)
			.keyword-list
				line-height: 1.375
				ul
					li
						+ li
							margin-top: 5px
						a
							font-size: r(15px)
							color: #666
			.product-suggest-list
				> .row
					margin-left: r(-20px)
					margin-bottom: r(-20px)
					> *
						padding-left: r(20px)
						padding-bottom: r(20px)
				.product-item
					background: #fff
					border: 1px solid #eeeeee
					border-radius: 5px
					padding: 10px
					.product-name
						&:hover
							text-decoration: underline
					.product-price 
						.price
							font-size: r(14px)
						.old-price 
							font-size: r(12px)
					.product-vnpay-price
						font-size: r(12px)
			.view-more-wrap
				text-align: center
				margin-top: 20px
				.btn
					height: 40px
					width: 220px
					border: 2px solid map-get($colors, main)
					color: map-get($colors, main)
					border-radius: 20px

	.bottom-header
		color: #fff
	.cart-wrapper
		margin-left: 30px
	.cart
		text-align: center
		position: relative
		.cart-toggle
			cursor: pointer
			.cart-amount
				width: 20px
				height: 20px
				top: -5px
				right: -8px
				font-size: 14px
		.cart-dropdown
			position: absolute
			top: 100%
			right: 0
			width: r(360px)
			background: #fff
			border-radius: 10px
			box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.05)
			margin-top: 10px
			z-index: 20
			display: none
			&.show
				display: block

		.cart-container
			padding: r(25px)

		.cart-heading
			display: flex
			align-items: center
			justify-content: space-between
			padding-bottom: r(12px)
			border-bottom: 3px solid #ebebeb
			margin-bottom: r(15px)

		.cart-close
			font-size: r(20px)
			cursor: pointer

		.cart-body
			overflow-y: auto
			height: r(265px)
			padding-right: 15px
			&::-webkit-scrollbar
				width: 4px
				background-color: #ccc
			&::-webkit-scrollbar-thumb
				background-color: map-get($colors, blue)
			ul
				li
					+ li
						margin-top: 15px
						padding-top: 15px
						border-top: 1px solid #ebebeb
		
		.cart-product
			.img
				display: block
				width: r(60px)
			.caption
				flex: 1
				text-align: left
				padding-left: r(20px)
			.name
				display: block
				margin-bottom: 10px
				&:hover
					color: map-get($colors, main)
			.quantity
				color: #999

		.cart-button
			margin-top: r(15px)
			padding-top: r(15px)
			border-top: 1px solid #ebebeb
			a
				height: r(40px)
				width: 100%
				background: linear-gradient(90deg, rgba(240,90,148,1) 0%, rgba(188,0,73,1) 100%)
				color: #fff
				border-radius: 20px
				em
					margin-left: r(15px)
	
	.menu
		border-left: 2px solid #fff
		ul
			display: flex
			li
				a
					display: flex
					align-items: center
					height: 40px
					padding: 0 r(20px)
					font-weight: 700
					transition: 0.3s all
				&.active,&:hover
					a
						background: #faaa3b
	@media screen and ( max-width: 1024.98px )
		.top-banner
			display: none
		.middle-header
			padding: 10px 0
			.row
				margin-left: 0
				margin-bottom: 0
				justify-content: space-between
				flex-wrap: nowrap
				> *
					padding-left: 0
					padding-bottom: 0
					flex: auto
			.col-lg-2
				flex: 0 0 40px
			.cart-wrapper
				flex: 0 0 50px
				margin-left: 15px
			.search-wrapper
				padding: 0 15px
			
		.menu-toggle
			span
				font-size: 36px

	@media screen and ( max-width: 576px )
		.middle-header
			.search-wrapper
				padding: 0 5px

.offcanvas-overlay
	position: fixed
	top: 0
	left: 0
	right: 0
	bottom: 0
	background: rgba(#000, 0.3)
	display: none
	z-index: 998
	
.menu-mobile-offcanvas
	width: 270px
	left: -270px
	background: #fff
	&.is-open
		transform: translate(270px,0,0)
	@media screen and ( min-width: 1024.98px )
		display: none
	.offcanvas-close
		display: none
	.offcanvas-wrap
		padding: r(40px) r(20px) r(40px)
		position: relative

@media screen and ( max-width: 1024.98px )
	.logo
		text-align: center
		margin-bottom: 20px
	.header-util-list
		flex-wrap: wrap
		font-size: 14px
		> *
			width: 100%
			margin-bottom: 10px
		.account-wrapper
			order: 1
		.order-check-wrapper
			order: 2
		.language-wrapper
			order: 3
			display: none
		.material-icons
			margin-right: 10px
			color: color(main)
	.top-header-util
		border-bottom: 1px solid #ccc
		margin-bottom: 20px
		padding-bottom: 5px
	.hotline-wrapper
		font-size: 16px
		padding-bottom: 20px
		margin-bottom: 20px
		border-bottom: 1px solid #ccc
	.bottom-header
		.container
			padding: 0
		.background-main
			background: transparent !important	
			display: block !important
	.menu
		ul
			li
				font-size: 14px
				+ li
					margin-top: 20px