.section
	padding: r(60px) 0
	@media screen and ( max-width: 768.98px )
		padding: r(40px) 0

.section-large
	padding: r(80px) 0
	@media screen and ( max-width: 768.98px )
		padding: r(40px) 0
		
.section-small
	padding: r(40px) 0

.section-grey
	background: #f8f8f8

.section-white
	background: #fff

.section-heading
	display: flex
	align-items: center
	justify-content: space-between
	padding-bottom: 10px
	border-bottom: 1px solid #eeeeee
	margin-bottom: r(24px)
	.section-title
		margin-bottom: 0
		font-size: r(24px)

.white-wrap
	border-radius: 10px
	background: #fff
	.section-heading
		padding: 10px
		margin-bottom: 0
		.section-title
			display: flex
			align-items: center
			.icon
				width: 40px
				height: 40px
				border-radius: 6px
				border: 1px solid #e5e5e5
				display: flex
				align-items: center
				justify-content: center
				margin-right: r(12px)
	.white-wrap-body
		padding: 20px 15px r(40px)