.deal-detail
	border-radius: 10px
	background-color: #fff
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	padding: r(40px)
	.section-title
		margin-bottom: r(16px)
	.brief
		margin-bottom: r(30px)
	.deal-product
		.item
			.img
				padding-top: 220 / 280 * 100%
				position: relative
				box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1)
				border-radius: 10px
				img
					height: 90%
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%,-50%)
			.title
				margin-top: r(15px)
				font-size: 14px
				line-height: 1.375
				font-weight: 500

.deal-form
	margin-top: r(60px)
	margin-bottom: r(20px)
	.wrap-form
		background: #fff
		border-radius: 10px
		padding: r(40px)
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

.wrap-form
	.form-group
		label
			display: none
		input[type="text"],textarea
			height: 38px
			border-radius: 20px
			border: 1px solid #ebebeb
			width: 100%
			padding: 0 r(20px)
			font-size: 14px
			background: #f8f8f8
			&::placeholder
				color: #666666
				font-size: 14px
		textarea
			height: r(150px)
			padding-top: 10px
	.frm-btn
		text-align: center
		input[type="submit"]
			height: 40px
			width: r(150px)
			border-radius: 20px
			background-color: color(main)
			color: #fff
			font-size: r(18px)
			font-weight: 700
			border: 0
			cursor: pointer
	.frm-btnwrap
		margin-top: r(30px)