.support-sidenav
	.root-title
		font-weight: 900
		font-size: r(24px)
		padding: 12px r(30px)
		color: #fff
		background: color(main)
		border-radius: 10px 10px 0 0
	ul
		border-radius: 0 0 10px 10px
		overflow: hidden
		li
			a
				display: flex
				align-items: center
				padding: r(15px) r(30px)
				background: #fff
				color: #999999
			+ li
				border-top: 1px solid #ebebeb
			&.active
				a
					color: #333
					font-weight: 500