.faq-item
	border-radius: 10px
	overflow: hidden
	border: 1px solid transparent
	background: #fff
	.faq-trigger
		display: flex
		align-items: center
		justify-content: space-between
		padding: r(16px) r(30px)
		cursor: pointer
		transition: 0.3s all
		.faq-title
			padding-right: r(30px)
			font-weight: 500
			line-height: 1.5
			display: flex
			.number
				color: #666666
				margin-right: r(15px)
		span
			font-size: r(24px)

	.faq-body
		display: none
		padding: r(30px)
		line-height: 1.5

	&.active
		border-color: color(main)
		.faq-trigger
			background: color(main)
			color: #fff
			.number
				color: #fff

	+ .faq-item
		margin-top: 10px

	@media screen and ( max-width: 576px )
		.faq-trigger,.faq-body
			padding: r(15px)