* + .btn-wrap
	margin-top: r(40px)

.view-more
	display: inline-flex
	align-items: center
	justify-content: space-between
	min-width: r(150px)
	height: r(40px)
	background: color(main)
	color: #fff
	text-transform: uppercase
	font-weight: 700
	padding: 0 r(18px)
	border-radius: r(20px)
	white-space: nowrap
	span
		width: 16px
		font-size: r(24px)

.swiper-btn
	position: absolute
	font-size: r(30px)
	top: 50%
	transform: translateY(-50%)
	cursor: pointer
	z-index: 2
	color: #999999
	@media screen and ( min-width: 1024.98px )
		margin: 0 15px
		&.swiper-prev
			right: 100%
			left: auto
			&.swiper-btn-inside
				right: auto
				left: 0
				margin: 0
		&.swiper-next
			left: 100%
			right: auto
			&.swiper-btn-inside
				left: auto
				right: 0
				margin: 0
	@media screen and ( max-width: 1024.98px )
		display: none

.btn
	height: 34px
	border-radius: 17px
	padding: 0 15px
	display: flex
	align-items: center
	justify-content: center
	background: color(main)
	color: #fff
	font-weight: 700