.home-8
	padding-top: r(20px) !important
	@media screen and ( min-width: 1024.98px )
		padding-bottom: 80px !important

.feedback-list
	margin-top: r(30px)
	.item
		padding: r(30px) r(40px)
		background: #fff
		border-radius: 10px
		position: relative
		&::before
			content: url(../img/quote-blur.png)
			position: absolute
			top: 0
			left: r(30px)
		.author
			margin-top: r(30px)
			display: flex
			line-height: 1.5
		.img
			width: r(110px)
			height: r(110px)
			border-radius: 100%
			overflow: hidden
			box-shadow: 0px 5px 25px 0px rgba(30, 45, 121, 0.15)
			img
				width: 100%
				height: 100%
				object-fit: cover
		.info
			flex: auto
			padding-left: r(20px)
		.subtitle
			font-size: r(14px)
		.name
			font-size: r(18px)
			font-weight: 700
			color: color(main)
	@media screen and ( min-width: 1024.98px )
		.item
			.img
				margin-bottom: -70px