.deal-item
	background: #fff
	overflow: hidden
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	margin-bottom: r(40px)
	.img
		img
			width: 100%
			height: 100%
			object-fit: cover
	.content-wrap
		padding: r(40px)
	.title
		font-size: r(24px)
		line-height: r(32px)
		font-weight: 700
		padding-bottom: 10px
		border-bottom: 1px solid color(main)
		margin-bottom: r(16px)
	.view-more
		margin-top: r(30px)
		height: r(34px)
		width: r(145px)
		text-transform: none
		font-size: 1rem
	@media screen and ( max-width: 1024.98px )
		.content-wrap
			padding: r(25px)