.news-detail
	padding: r(30px)
	background: #fff
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

.news-detail-title
	font-size: r(24px)
	color: color(main)
	font-weight: 900
	line-height: 1.5
	margin-bottom: r(20px)

.news-detail-meta 
	display: flex
	align-items: center
	justify-content: space-between
	padding-bottom: 10px
	border-bottom: 1px solid #e0e0e0
	.date
		font-size: r(14px)
		color: #666666

.news-detail-content
	padding-top: r(24px)

.news-detail-other
	padding: r(40px) 0 r(80px)
	@media screen and ( max-width: 1024.98px )
		padding: r(40px) 0