.product-category
	width: 250px
	
	.product-category-toggle
		height: 40px
		cursor: pointer
		padding-left: 20px
		em
			font-size: 24px
		strong
			margin: 0 5px

	.product-category-dropdown
		left: 0
		right: 0
		border: 1px solid #e5e5e5
		z-index: 20

	.product-category-list
		padding: 0
		color: #333
		> ul
			> li
				padding: 12px 20px
				border-right: 0
				transition: 0.3s all
				+ li
					border-top: 1px solid #ccc

	.has-sub
		position: relative
		display: flex
		align-items: center
		justify-content: space-between
		.toggle
			font-size: 16px
		.sub-menu
			position: absolute
			left: 100%
			top: -1px
			white-space: nowrap
			display: none
			min-width: 100%
			ul
				background: #e5e5e5
				padding: r(20px)
				line-height: 1.5
				li
					+ li
						margin-top: 5px
		&:hover
			.sub-menu
				display: block

	@media screen and ( max-width: 1024.98px )
		width: 100%
		padding-bottom: 20px
		margin-bottom: 20px
		border-bottom: 1px solid #ccc
		.product-category-toggle
			display: none
		.product-category-dropdown
			position: static
			display: block
			border: 0
		.product-category-list
			box-shadow: none
			ul
				li
					padding: 0
					font-size: 14px
					+ li
						margin-top: 20px
						border-top: 0
		.has-sub
			flex-wrap: wrap
			.sub-menu
				width: 100%
				position: static
				margin-top: 10px