.home-2
	padding-top: 0 !important
	
.flash-deal-slider
	.product-item
		background: #fff
		border-radius: 5px
		border-right: 0
		.product-icon
			top: 10px
			right: 10px

.deal-progress-bar
	height: 8px
	border-radius: 4px
	background-color: #eeeeee
	overflow: hidden
	.deal-progress-bar-inner
		background: color(main)
		transition: 0.3s all
		height: 100%
		border-radius: 5px
		
.deal-progress-bar-text
	font-size: 12px
	margin-top: 10px