footer
	background-color: #fff
	@media screen and ( max-width: 1024.9px )
		padding-bottom: 60px
.footer-subscription
	padding: 26px 0
	background-position: center
	color: #fff
	.subscription-title
		.icon
			height: r(48px)
			em
				font-size: r(48px)
		.text
			line-height: 1.3
			padding-left: 15px
			h3,.title
				text-transform: uppercase
				font-size: r(18px)
				font-weight: 700

	.subscribefrm
		display: flex
		flex-wrap: wrap
		overflow: hidden
		border-radius: 20px
		.fa-exclamation-triangle
			width: 100%
			color: #fff
		input
			height: 40px
			border: 0
			background: #fff
			padding: 0 15px
			font-size: 14px
			flex: 1 1 0%
			&::placeholder
				color: #999
				font-style: italic
		button
			width: r(180px)
			height: 40px
			background-color: #faaa3b
			color: #fff
			display: flex
			align-items: center
			justify-content: center
			font-weight: 700
			text-transform: uppercase
			border: 0
	@media screen and ( max-width: 768.98px )
		.row
			margin: 0 0 r(-15px) r(-10px)
			> *
				padding: 0 0 r(15px) r(10px)
		.subscribefrm
			button
				width: 90px
		.subscription-title
			.icon
				display: none
			.text
				padding-left: 0
				p
					display: none
				h3
					font-size: r(24px)

.footer-bottom
	padding: r(40px) 0
	
	.footer-item
		.footer-title
			font-size: r(18px)
			font-weight: 700
			line-height: r(24px)
			margin-bottom: r(20px)
			display: flex
			flex-wrap: wrap
			span
				font-size: r(12px)
				margin-left: 3px
		+ .footer-item
			margin-top: r(30px)

		&.footer-info
			.footer-title
				margin-bottom: r(5px)
			a
				font-size: r(24px)
				text-transform: uppercase
				display: flex
				line-height: 1.2
				align-items: baseline
				em
					color: #ccc
					display: inline-block
					font-size: r(30px)
					position: relative
					top: 4px
				strong
					color: map-get($colors, main)
					margin: 0 0 0 6px
					display: flex
					flex-wrap: wrap
					align-items: baseline
					font-weight: 900
				span
					font-weight: 700
					font-size: r(12px)
					color: map-get($colors, main)
					margin-left: 5px

		&.footer-social
			.footer-title
				margin-bottom: r(10px)
			ul
				display: flex
				li
					+ li
						margin-left: 15px

		&.footer-nav
			ul
				li
					a
						line-height: 1.4
						font-size: r(15px)
						color: #666
					+ li
						margin-top: r(10px)

.footer-copyright
	background-color: #f8f8f8
	padding: r(30px) 0
	border-top: 1px solid #e5e5e5
	.row
		+ .row
			margin-top: 10px
	.company-info,.copyright,.web-term
		font-size: r(14px)
		line-height: r(20px)
		color: #666
	.bct
		text-align: right
	.copyright,.web-term
		color: #999
	.web-term
		text-align: right
		span
			margin: 0 5px
	@media screen and ( max-width: 768.98px )
		.web-term,.bct,.company-info,.copyright,.web-term
			text-align: left
			font-size: r(18px)
			line-height: 1.375
		.bct
			margin-top: r(10px)