.home-6
	background-image: url(../img/home-bg-6.jpg)
	background-repeat: no-repeat
	background-position: center
	background-size: cover
	color: #fff
	.item
		.icon
			display: flex
			align-items: center
			justify-content: center
			width: r(147px)
			height: r(147px)
			border-radius: 50%
			border: 1px solid #fff
			margin: 0 auto
		.title
			font-size: r(20px)
			text-align: center
			margin-top: r(15px)
			line-height: 1.375
	@media screen and ( min-width: 1024.98px )
		min-height: 500px
		display: flex
		align-items: center
		.container
			width: 100%