.about-1
	.about-content-1
		border-radius: 10px
		overflow: hidden
		background: #fff
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
		.content
			padding: r(30px)
		.section-title
			margin-bottom: r(12px)
		.img
			img
				width: 100%
				height: 100%
				object-fit: cover
	.about-content-2
		margin-top: r(30px)
		border-radius: 10px
		overflow: hidden
		.img
			text-align: center
			margin-bottom: r(30px)
		.content-wrap
			padding: 30px
			width: 100%
			background: #fff
	@media screen and ( min-width: 1024.98px )
		.about-content-2
			height: r(360px)
			background: url(../img/bg-about-1.jpg)
			background-repeat: no-repeat
			background-size: contain
			background-position: center
			display: flex
			align-items: center
			.content-wrap
				padding: 0
			.content
				padding: r(30px)
			.img-wrap
				position: relative
				.img
					position: absolute
					right: 0
					top: 50%
					transform: translateY(-50%)


.about-2
	padding-top: r(20px) !important
	.section-title
		margin-bottom: r(12px)

.partner-slider
	margin-top: r(30px)
	background: #fff
	border-radius: 10px
	.img
		display: flex
		align-items: center
		justify-content: center
		flex-direction: column
		height: r(190px)
		padding: r(15px)
		.logo
			height: 60px
			display: flex
			align-items: center
			justify-content: center
			margin-bottom: r(16px)
			img
				max-height: 100%
		.title
			text-align: center
			line-height: 1.375

	@media screen and ( min-width: 1024.98px )
		.img
			.title
				&:hover
					color: color(main)

.about-3
	padding-top: 0 !important
	.item
		border-radius: 10px
		overflow: hidden
		line-height: 1.5
	.title
		font-weight: 700
		font-size: r(18px)
		margin-bottom: r(12px)
	.address
		display: flex
		align-items: center
		font-size: r(14px)
		margin-bottom: r(20px)
		&:before
			font-family: Material Icons
			content: "location_on"
			margin-right: r(8px)
			color: color(main)
			font-size: r(30px)
			height: r(30px)
			display: flex
			align-items: center
	.phone
		display: flex
		align-items: center
		font-size: r(14px)
		&:before
			font-family: Material Icons
			content: "phone"
			margin-right: r(8px)
			color: color(main)
			font-size: r(30px)
			height: r(30px)
			display: flex
			align-items: center
	@media screen and ( min-width: 1024.98px )
		.item
			height: 190px
			position: relative
			background: #000
			box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1)
			.img
				opacity: 0.5
				height: 100%
				img
					height: 100%
					width: 100%
					object-fit: cover
			.content
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
				color: #fff
				padding: r(30px)
	@media screen and ( max-width: 1024.98px )
		.item
			.content
				padding: 15px
				background: #fff