.home-banner
	.row
		margin-left: -5px
		margin-bottom: -5px
		> *
			padding-left: 5px
			padding-bottom: 5px

	.big-banner
		position: relative
		.item
			.img
				+img-scale(450 / 974 * 100%)
		.swiper-pagination
			bottom: 20px
			right: 20px
			left: auto
			width: auto
			.swiper-pagination-bullet
				height: 10px
				width: 10px
				background-color: transparent
				border: 2px solid #e1e1e1
				opacity: 1
				&.swiper-pagination-bullet-active
					background-color: map-get($colors, main)
	.small-banner
		display: flex
		flex-direction: column
		height: 33.33%
		margin-bottom: -5px
		.item
			margin-bottom: 5px
			.img
                +img-scale(149 / 250 * 100%)
	@media screen and ( min-width: 1024.98px )
		.big-banner-col
			width: 974 / 1230 * 100%
		.small-banner-col
			width: 250 / 1230 * 100%
	@media screen and ( max-width: 1024.98px )
		.small-banner-col
			display: none