.product-filter-heading
	margin-bottom: r(16px)
	.filter-title
		font-size: r(18px)

.product-filter-item
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	padding: 15px
	background: #fff
	border-radius: 10px
	+ .product-filter-item
		margin-top: 20px

	.product-filter-title
		cursor: pointer
		.text
			font-size: r(18px)
		&.active
			.arrow
				transform: rotate(180deg)


	.product-filter-body
		padding-top: 20px
		display: none

	.product-filter-group
		ul
			li
				margin-bottom: 10px
				&:nth-child(n+5)
					display: none
				.filter-link 
					line-height: 1.2
					display: flex
					align-items: center
					.check-box
						margin-right: 5px
						position: relative
						display: inline-block
						position: relative
						top: -1px
						height: 20px
						&:before
							font-size: 20px
							font-family: remixicon
							content: "\EB7E"
							color: #f4f4f4
					.text
						display: inline-block
						font-size: 16px
						text-transform: uppercase
						color: #666666
					&.active
						.check-box
							&:before
								content: "\EB82"
								color: map-get($colors, main)

	.filter-view-more
		display: block
		margin-top: r(20px)
		color: color(main)

@media screen and ( max-width: 1024.9px )
	.filter-col
		padding-bottom: 0
	.product-filter-wrap
		display: none
		position: fixed
		z-index: 1000
		background: #fff
		top: 0
		bottom: 0
		left: 0
		width: 100%
		padding: 0
		overflow: auto
	.product-filter-container
		overflow: auto
		height: calc( 100% - 48px - 55px )
	.product-filter-heading
		height: 48px
		background: color(main)
		color: #fff
		> *
			height: 100%
			padding: 0 15px
		.filter-close
			font-size: 30px
			position: relative
			top: 2px
		.filter-title
			font-size: 16px
			em
				margin-right: 5px
		.filter-reset
			font-size: 14px
	.product-filter-item
		padding: 20px 15px
		border-radius: 0
		+ .product-filter-item
			margin-top: 0
			border-top: 5px solid #e5e5e5
		.product-filter-body
			display: block
			padding: 0
		.product-filter-title
			height: auto
			background-image: none
			background-color: transparent
			border-bottom: 0
			padding: 0
			margin-bottom: 20px
			font-size: 16px
			.arrow
				display: none
		.product-filter-group
			ul
				display: flex
				flex-wrap: wrap
				margin: 0 -5px
				li
					width: 50%
					border-bottom: 0
					margin-bottom: 10px
					padding: 0 5px
					.filter-link
						display: flex
						align-items: center
						justify-content: center
						height: 30px
						border: 1px solid #e5e5e5
						border-radius: 6px
						position: relative
						overflow: hidden
						.check-box
							display: none
						&.active
							border-color: map-get($colors, main)
							&:before
								content: url(../img/filter-active.png)
								top: 0
								right: 0
								position: absolute
	.product-filter-price
		.price-filter-input-wrap
			display: flex
			flex-wrap: wrap
			justify-content: space-between
			input
				width: calc(50% - 5px)
		.price-filter-input-btn
			width: 100%
	.product-filter-apply
		height: 55px
		display: flex
		align-items: center
		box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1)
		.filter-apply
			height: 40px
			width: 200px
			margin: 0 auto
			background: linear-gradient(90deg,#f05a94,#bc0049)
			color: #fff
			font-weight: 700
			border-radius: 20px