.product-detail-top
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	border-radius: 10px
	padding: r(30px) r(50px)
	background: #fff
	@media screen and ( max-width: 768.98px )
		padding: r(20px)

.product-detail-images
	position: relative
	.img
		height: r(480px)
		position: relative
		display: block
		img
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%,-50%)
			max-height: 100%
	@media screen and ( min-width: 1024.98px )
		.img
			max-width: r(450px)
			margin: 0 auto

.product-detail-discount
	position: absolute
	top: 0
	right: r(30px)
	display: flex
	align-items: center
	justify-content: center
	width: r(70px)
	height: 35px
	background-color: #e7781c
	font-size: r(18px)
	font-weight: 700
	color: #fff
	text-transform: uppercase
	z-index: 2

.product-detail-thumbnail
	margin-top: r(20px)
	.img
		height: r(120px)
		display: flex
		align-items: center
		justify-content: center
		border: 1px solid #ebebeb
		overflow: hidden
		cursor: pointer
		img
			max-height: 80%
	.swiper-slide-thumb-active
		.img
			border-color: map-get($colors, main)
	@media screen and ( min-width: 1024.98px )
		padding: 0 r(30px) 0 0
		.swiper-scrollbar
			opacity: 0
		&:hover
			.swiper-scrollbar
				opacity: 1

.product-detail-title
	font-size: r(30px)
	font-weight: 700
	line-height: 1.375
	margin-bottom: r(20px)

.product-detail-price
	.price
		font-size: r(30px)
	.old-price
		color: #999999
		font-size: r(18px)
		margin-left: r(20px)
		
.product-detail-rating
	display: flex
	.rating-number
		margin-left: 5px
		color: #999999

.product-detail-data
	margin-top: r(30px)
	padding-top: r(20px)
	border-top: 1px solid #ebebeb
	line-height: 1.5
	table
		width: 100%
		tr
			td
				&:first-child
					color: color(main)
					font-weight: 700
					padding-right: 12px
					white-space: nowrap
			+ tr
				td
					padding-top: 10px
	.status
		&.available
			color: #0c9bff

.product-detail-opt-list
	margin-top: r(20px)
	padding-top: r(24px)
	border-top: 1px solid #ebebeb
	.opt-list
		display: flex
		align-items: center
		flex-wrap: wrap
		.title
			width: 20%
			padding-right: r(15px)
			line-height: 1.5
			white-space: nowrap
		ul
			display: flex
			align-items: center
			flex-wrap: wrap
			margin-left: r(-15px)
			margin-bottom: r(-15px)
			li
				padding-left: r(15px)
				padding-bottom: r(15px)
				a
					display: flex
					align-items: center
					justify-content: center
					min-width: r(80px)
					height: 30px
					border-radius: 15px
					background: #f5f5f5
					color: #999999
					padding: 0 15px
					&.active
						background: color(main)
						color: #fff
		+ .opt-list
			margin-top: r(30px)	
	@media screen and ( max-width: 576px )
		.opt-list
			.title
				width: 100%
				margin-bottom: 8px
			+ .opt-list
				margin-top: r(20px)	

.product-detail-quantity
	margin-top: r(20px)
	padding-top: r(24px)
	border-top: 1px solid #ebebeb
	display: flex
	align-items: center
	strong
		width: 20%
		padding-right: r(15px)
		white-space: nowrap
	@media screen and ( max-width: 576px )
		strong
			width: auto

.product-detail-btn
	margin-top: r(20px)
	display: flex
	justify-content: space-between
	a
		display: flex
		align-items: center
		justify-content: center
		flex-direction: column
		height: r(60px)
		width: calc(50% - 10px)
		border-radius: r(30px)
		color: #fff
		text-transform: uppercase
		&.btn-buy-now
			background: #f60e10
			span
				display: block
				line-height: 2
				font-size: 12px
		&.btn-addtocart
			background: color(main)
			text-transform: uppercase
			font-size: r(18px)
			font-weight: 500
	@media screen and ( max-width: 576px )
		flex-wrap: wrap
		a
			width: 100%
			+ a
				margin-top: r(20px)

.product-detail-bottom
	margin-top: r(60px)
	@media screen and ( max-width: 768px )
		margin-top: r(40px)

.product-detail-info
	.title
		height: r(50px)
		padding: 0 r(30px)
		display: inline-flex
		align-items: center
		color: color(main)
		font-weight: 700
		font-size: r(18px)
		white-space: nowrap
		border-radius: 10px 10px 0px 0px
		background: #fff
	.content
		padding: r(30px)
		background: #fff
		border-radius: 0px 0px 10px 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
	.expand-content
		max-height: 530px
		
.product-detail-related
	border-radius: 10px
	padding: r(30px) 0
	background: #fff
	margin-top: r(40px)
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

.spin-input-wrap
	display: inline-flex
	height: 40px
	border-radius: 20px
	overflow: hidden
	.spin-btn
		width: r(60px)
		display: flex
		align-items: center
		justify-content: center
		background-color: #f8f8f8
		cursor: pointer
		color: #c3c3c3
	input
		width: r(40px)
		text-align: center
		border: 0
		font-size: 16px
		background-color: #f8f8f8

.rating-star
	width: 67px
	height: 13px
	background-image: url(../img/star-group.png)
	background-repeat: no-repeat
	div
		background-image: url(../img/star-group-on.png)
		background-repeat: no-repeat
		height: 100%