.dealer-wrap
	.section-title
		margin-bottom: r(16px)
	.map
		width: 100%
		height: 100%
		overflow: hidden
		border-radius: 10px
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1)
		#map_canvas,#mapiframe
			height: r(500px)
			width: 100%
			img
				width: 100%
				height: 100%
	.result-list
		border-radius: 10px
		background: #fff
		padding: r(30px)
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1)
		ul
			max-height: r(330px)
			overflow-y: auto
			padding-right: r(20px)
			&::-webkit-scrollbar
				background-color: #e1e1e1
				width: 3px
				border-radius: 1.5px
			&::-webkit-scrollbar-thumb
				border-radius: 1.5px
				background-color:  color(main)
			li
				border-radius: 10px
				padding: r(18px)
				line-height: 1.5
				border: 1px solid #ebebeb
				cursor: pointer
				.name
					font-weight: 700
					font-size: r(18px)
					margin-bottom: r(12px)
				.address
					display: flex
					font-size: r(14px)
					margin-bottom: r(20px)
					&:before
						font-family: Material Icons
						content: "location_on"
						margin-right: r(8px)
						color: #ccc
						font-size: r(30px)
						height: r(30px)
						display: flex
						align-items: center
				.phone
					display: flex
					font-size: r(14px)
					&:before
						font-family: Material Icons
						content: "phone"
						margin-right: r(8px)
						color: #ccc
						font-size: r(30px)
						height: r(30px)
						display: flex
						align-items: center
				+ li
					margin-top: r(20px)
				&.active
					.name
						color: color(main)
					.address:before,.phone:before
						color: color(main)
	@media screen and ( min-width: 1024.98px )
		.section-title
			padding-left: r(30px)
		.map
			margin-left: -30px
		.result-wrapper
			margin-right: -60px
			position: relative
			z-index: 2
			padding-top: 30px

.contact-form-section
	padding-top: 0 !important

.contact-form
	padding: r(50px) r(30px)
	background: #f8f8f8
	border-radius: 10px
	@media screen and ( max-width: 1024.98px )
		padding: r(30px)

.wrap-contact
	.form-group
		label
			display: none
		input[type="text"],textarea
			height: 40px
			border-radius: 20px
			border: 1px solid #e0e0e0
			width: 100%
			padding: 0 r(20px)
			font-size: 14px
			&::placeholder
				color: #666666
				font-size: 14px
		textarea
			height: r(150px)
			padding-top: 10px
	.frm-btn
		input[type="submit"]
			height: 40px
			width: r(150px)
			border-radius: 20px
			background-color: color(main)
			color: #fff
			font-size: r(18px)
			font-weight: 700
			border: 0
			cursor: pointer
