.review-item
	.review-content
		background: #fff
		padding: r(30px)
		border-radius: 10px
		position: relative
		&::before
			content: url(../img/quote-blur.png)
			position: absolute
			top: 0
			left: r(30px)
	.review-img
		box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)
		border-radius: 10px
		overflow: hidden
		position: relative
		.video
			position: absolute
			display: flex
			align-items: center
			justify-content: center
			width: r(78px)
			height: r(30px)
			border-radius: r(10px)
			background: color(main)
			color: #fff
			font-weight: 500
			bottom: 10px
			right: 10px
			em
				margin-right: 5px
			span
				font-size: r(15px)
	.title 
		font-size: r(18px)
		font-weight: 700
	.author
		margin-top: r(24px)
		span
			font-size: r(14px)
		.name
			font-size: r(18px)
			font-weight: 700
			color: color(main)
	+ .review-item
		margin-top: r(40px)
		padding-top: r(40px)
		border-top: 1px solid #e0e0e0
	@media screen and ( min-width: 1024.98px )
		.review-content
			padding: r(40px)
		&:nth-child(2n)
			.row
				flex-direction: row-reverse
	@media screen and ( max-width: 1024.98px )
		border-radius: 10px
		overflow: hidden
		.row
			margin-bottom: 0
			> *
				padding-bottom: 0
		.review-img
			border-radius: 0
		.review-content
			border-radius: 0