.product-list-wrap
	background-color: #fff
	border-radius: 10px
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.03)

.product-list-heading
	padding: 20px
	border-bottom: 1px solid #eeeeee

.product-list-title
	margin-bottom: 0 !important

.product-list-util
	select
		font-size: 1rem
		appearance: none
		background-image: url(../img/sl.png)
		background-repeat: no-repeat
		background-position: 90% center
		background-color: transparent
		height: 30px
		border-radius: 10px
		border: 1px solid #ebebeb
		padding: 0 15px
		font-size: 14px
		min-width: 80px	

.product-list-category
	display: flex
	align-items: center
	flex-wrap: wrap
	margin-bottom: r(40px)
	padding: r(20px)
	span
		font-weight: 500
		margin-right: r(20px)
		line-height: 2
	a
		display: flex
		align-items: center
		justify-content: center
		height: 30px
		min-width: r(80px)
		font-size: 14px
		padding: 0 12px
		border: 1px solid #ebebeb
		border-radius: 10px
	ul
		display: flex
		flex-wrap: wrap
		margin-bottom: r(-20px)
		margin-left: r(-20px)
		> *
			padding-left: r(20px)
			padding-bottom: r(20px)
		li
			a
				&.active
					background: color(main)
					border-color: color(main)
					color: #fff

.product-list
	padding: 0 r(20px)
	.row
		margin-bottom: 0
		margin-left: 0
		> *
			padding-left: 0
	@media screen and ( max-width: 576px )
		padding: 0

.product-item
	position: relative
	padding: r(20px)
	border-right: 1px solid #eeeeee
	.product-img
		padding-top: 190 / 200 * 100%
		position: relative
		display: block
		img
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%,-50%)
			max-height: 90%
	.product-icon
		position: absolute
		top: 0
		right: r(20px)
		.icon
			display: flex
			align-items: center
			justify-content: center
			width: r(40px)
			height: r(40px)
			border-radius: 4px
			text-align: center
			font-size: 12px
			text-transform: uppercase
			z-index: 2
			&.icon-new
				background: #e64b39
				color: #fff
	.product-discount
		position: absolute
		bottom: 0
		left: 0
		display: flex
		align-items: center
		justify-content: center
		width: r(80px)
		height: 23px
		border-radius: 4px
		background-color: #e7781c
		font-size: 12px
		color: #fff
		text-transform: uppercase
	.product-name
		font-size: 14px
		margin-top: 10px
		a
			+line(2)
			height: 38px
	.product-price
		line-height: 1.5
		> *
			display: block
		.price
			font-size: r(18px)
		.old-price
			font-size: r(14px)
			color: #999999
	.product-deal-progress
		margin-top: r(10px)