.breadcrumb-wrapper
	border-bottom: 1px solid #ebebeb
	.breadcrumb
		display: flex
		align-items: center
		padding: 12px 0
		list-style: none
		padding-left: 0
		margin-bottom: 0
		li
			a
				font-size: 14px
				color: #999
				display: flex
				align-items: center
			+ li
				padding-left: 12px
				display: flex
				align-items: center
				&::before
					content: "|"
					margin-right: 12px

